export default class GameElement {
    constructor (game) {
        this.game = game
        this.setup()
    }

    static instance(...args) {
        this.i = this.i || new this(...args)
        this.i.init()
        return this.i
    }

    static new(...args) {
        return new this(...args)
    }

    setup() {}

    init() {}

    //  draw
    draw() {}

    update() {}
}
